import React from 'react';
import {connect} from 'react-redux';
import './App.css';
import jetplane from './jetplane.png';  
import arrow2 from './arrow2.png';
import arrow1 from './arrow1.png';
import arrow3 from './arrow3.png';




class App extends React.Component {
	constructor(props) {
		super(props);
		
		
	}
	
	componentDidMount(){
		document.getElementById('jet-plane').style.left=this.props.jetPosition.leftDistance;
		document.getElementById('jet-plane').style.bottom=this.props.jetPosition.bottomDistance;
		document.addEventListener('keydown',this.pressFunction);
		setInterval(this.arrowMover,80);
		
	}
	

	
	
	
	componentDidUpdate() {
		document.getElementById('jet-plane').style.left = this.props.jetPosition.leftDistance;
		document.getElementById('jet-plane').style.bottom = this.props.jetPosition.bottomDistance;
		
	}
	arrowMover = () => {
		for (var eachArrow in this.props.arrowPositions){
			if (this.props.arrowPositions[eachArrow].initialized=='no') {
				this.props.dispatch({type:'initializeArrow',payload:eachArrow});
				break;
		}else if(this.props.arrowPositions[eachArrow].life<=18){
			this.props.dispatch({type:'moveArrow',payload:eachArrow});
			
		}else {
			this.props.dispatch({type:'initializeArrow',payload:eachArrow});
		}
		}
	}
	
	
	pressFunction=(event) => {
		this.props.dispatch({type:'moveJet', payload:event.key});
		
	};
	
	mouseDown= (event) => {
		this.props.dispatch({type:'mouseDown',pageY:event.pageY,pageX:event.pageX});
	}
	
	mouseMove= (event) => {
		if (parseFloat(this.props.jetPosition.bottomDistance)>40 || (parseFloat(this.props.jetPosition.bottomDistance)<0) ){
			this.props.dispatch({type:'mouseYOutside',pageY:event.pageY});
		}else{
			this.props.dispatch({type:'mouseY',pageY:event.pageY});
		}
		
		
	}
	
	render() {
		let arrows=this.props.arrowPositions;
		let myStyle=[
		{display:arrows.firstArrow.display,left:arrows.firstArrow.leftDistance,bottom:arrows.firstArrow.bottomDistance,},
		{display:arrows.secondArrow.display,left:arrows.secondArrow.leftDistance,bottom:arrows.secondArrow.bottomDistance,},
		{display:arrows.thirdArrow.display,left:arrows.thirdArrow.leftDistance,bottom:arrows.thirdArrow.bottomDistance,},
		{display:arrows.fourthArrow.display,left:arrows.fourthArrow.leftDistance,bottom:arrows.fourthArrow.bottomDistance,},
		{display:arrows.fifthArrow.display,left:arrows.fifthArrow.leftDistance,bottom:arrows.fifthArrow.bottomDistance,},
		{display:arrows.sixthArrow.display,left:arrows.sixthArrow.leftDistance,bottom:arrows.sixthArrow.bottomDistance,},
		{display:arrows.seventhArrow.display,left:arrows.seventhArrow.leftDistance,bottom:arrows.seventhArrow.bottomDistance,},
		{display:arrows.eighthArrow.display,left:arrows.eighthArrow.leftDistance,bottom:arrows.eighthArrow.bottomDistance,},
		{display:arrows.ninthArrow.display,left:arrows.ninthArrow.leftDistance,bottom:arrows.ninthArrow.bottomDistance,},
		{display:arrows.tenthArrow.display,left:arrows.tenthArrow.leftDistance,bottom:arrows.tenthArrow.bottomDistance,},
		{display:arrows.eleventhArrow.display,left:arrows.eleventhArrow.leftDistance,bottom:arrows.eleventhArrow.bottomDistance,},
		{display:arrows.twelfthArrow.display,left:arrows.twelfthArrow.leftDistance,bottom:arrows.twelfthArrow.bottomDistance,},
		{display:arrows.thirteenthArrow.display,left:arrows.thirteenthArrow.leftDistance,bottom:arrows.thirteenthArrow.bottomDistance,},
		{display:arrows.fourteenthArrow.display,left:arrows.fourteenthArrow.leftDistance,bottom:arrows.fourteenthArrow.bottomDistance,},
		{display:arrows.fifteenthArrow.display,left:arrows.fifteenthArrow.leftDistance,bottom:arrows.fifteenthArrow.bottomDistance,},
		{display:arrows.sixteenthArrow.display,left:arrows.sixteenthArrow.leftDistance,bottom:arrows.sixteenthArrow.bottomDistance,},
		{display:arrows.seventeenthArrow.display,left:arrows.seventeenthArrow.leftDistance,bottom:arrows.seventeenthArrow.bottomDistance,},
		{display:arrows.eighteenthArrow.display,left:arrows.eighteenthArrow.leftDistance,bottom:arrows.eighteenthArrow.bottomDistance,},
		{display:arrows.nineteenthArrow.display,left:arrows.nineteenthArrow.leftDistance,bottom:arrows.nineteenthArrow.bottomDistance,},
		
		];
		return (
		
			<div id='main-canvas' 
			onMouseMove={this.mouseMove}
			onMouseDown={this.mouseDown}
			>
				<img id='jet-plane' 
				src={jetplane} 
				alt='jet plane' 
				
				
				/>
				<img 
					className='arrow' 
					id='arrow1' 
					src={arrow1}
					style={myStyle[0]}
					
				/>
				<img 
					className='arrow' 
					id='arrow2' 
					src={arrow2}
					style={myStyle[1]}
				/>
				<img 
					className='arrow' 
					id='arrow3' 
					src={arrow3}
					style={myStyle[2]}
				/>
				<img 
					className='arrow' 
					id='arrow4' 
					src={arrow1}
					style={myStyle[3]}
				/>
				<img 
					className='arrow' 
					id='arrow5' 
					src={arrow2}
					style={myStyle[4]}
				/>
				<img 
					className='arrow' 
					id='arrow6' 
					src={arrow3}
					style={myStyle[5]}
				/>
				<img 
					className='arrow' 
					id='arrow7' 
					src={arrow1}
					style={myStyle[6]}
				/>
				<img 
					className='arrow' 
					id='arrow8' 
					src={arrow2}
					style={myStyle[7]}
				/>
				<img 
					className='arrow' 
					id='arrow9' 
					src={arrow3}
					style={myStyle[8]}
				/>
				<img 
					className='arrow' 
					id='arrow9' 
					src={arrow1}
					style={myStyle[9]}
				/>
				<img 
					className='arrow' 
					id='arrow9' 
					src={arrow2}
					style={myStyle[10]}
				/>
				<img 
					className='arrow' 
					id='arrow9' 
					src={arrow3}
					style={myStyle[11]}
				/>
				<img 
					className='arrow' 
					id='arrow9' 
					src={arrow1}
					style={myStyle[12]}
				/>
				<img 
					className='arrow' 
					id='arrow9' 
					src={arrow2}
					style={myStyle[13]}
				/>
				<img 
					className='arrow' 
					id='arrow9' 
					src={arrow3}
					style={myStyle[14]}
				/>
				<img 
					className='arrow' 
					id='arrow9' 
					src={arrow1}
					style={myStyle[15]}
				/>
				<img 
					className='arrow' 
					id='arrow9' 
					src={arrow2}
					style={myStyle[16]}
				/>
				<img 
					className='arrow' 
					id='arrow9' 
					src={arrow3}
					style={myStyle[17]}
				/>
				<img 
					className='arrow' 
					id='arrow9' 
					src={arrow1}
					style={myStyle[18]}
				/>
			</div>
		)
		
	}
	
}

function mapStateToProps(state){
	return {
			jetPosition:state.jetPosition,
			arrowPositions:state.arrowPositions,
	}
}
	

export default connect (mapStateToProps) (App);
